.houses_page{
    width: 100%;
    margin: 0 auto 1.8rem;
    .city_title{
        width: 100%;
        line-height: 1rem;
        background: #fff;
        box-shadow: 0px 1px 0.1rem rgba(0, 0, 0, 0.01), 0px 1px 2px rgba(0, 0, 0, 0.12);
        position: fixed;
        top: 0;
        left: 0;
        z-index: 100;
        i{
            font-weight: bold;
            font-size: 0.3rem;
        }
        span{
            font-size: 0.3rem;
            color: #008489;
            margin-left: 0.2rem;
        }
    }
    .preview_tags{
        width: 100%;
        padding: 0.25rem 0;
        background: #fff;
        position: fixed;
        top: 1rem;
        left: 0;
        z-index: 99;
        transition: all 0.3s;
        ul{
            white-space: nowrap;
            overflow-x:scroll;
            touch-action: pan-x;
            li{
                text-align: center;
                font-size: 0.24rem;
                display: inline-block;
                padding: 4px 8px;
                background-color: #f0f0f0;
                border:1px solid #f0f0f0;
                border-radius:30px;
                margin-right: 0.2rem;
            }
            .on{
                background-color: #fff;
                border-color: #008489;
                color: #008489;
            }
        }
    }
    .city_houses{
        width: 100%;    
        margin-top: 2.2rem;  
        h2 {
            font-size: 0.32rem;
            font-weight: bold;
            text-align: center;
            color: #666;
        }
        .one_houses{
            width: 100%;
            border: 1px solid #fff;
            border-radius: 16px;
            box-shadow: 0 6px 30px rgba(0, 0, 0, 0.15);
            margin-bottom: 16px;
            padding: 4px;
            box-sizing: border-box;
            .house_picture{
                width: 100%;
                border-radius: 8px 8px 0 0;
            }
            .house_txt{
                width: 100%;
                position: relative;
                .user_picture{
                    position: absolute;
                    right: 0.3rem;
                    top:-0.65rem;
                    width:0.8rem;
                    height: 0.8rem;
                    border: 2px solid #fff;
                    border-radius:50%;
                }
                .house_body{
                    width:92%;
                    margin: 0.2rem auto 0;
                    .kicker_content{
                        font-size: 0.26rem;
                        color: #767676;
                        font-weight: bold;
                        line-height: 0.4rem;
                    }
                    h2{
                        font-weight: bold;
                        color: #484848;
                        white-space: nowrap;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        line-height: 0.4rem;
                    }
                    .house_tags{
                        display: flex;
                        flex-wrap: wrap;
                        margin: 0.1rem 0;
                        li{
                            text-align: center;
                            font-size: 0.24rem;
                            display: inline-block;
                            padding: 5px 12px;
                            background-color: #f0f0f0;
                            border-radius:13px;
                            margin-right: 0.1rem;
                            font-weight: bold;
                            margin-bottom: 0.1rem;
                        }
                        .first{
                            color: #296E00;
                            background-color: #EAF7EA;
                        }
                        .second{
                            color: #8A2400;
                            background-color: #f7f3ea;
                        }
                    }
                }
                .house_foot{
                    width: 100%;
                    border-top: 1px solid #ebebeb;
                    .house_footer{
                        width:92%;
                        line-height: 1rem;
                        margin: 0 auto;
                        display: flex;
                        justify-content: space-between;
                        span{
                            font-size: 0.26rem;
                            b{
                                font-size: 0.32rem;
                                margin-right: 0.1rem;
                            }
                        }
                    }
                }
            }
        }
    }
}